import { format } from "date-fns"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { Box, TooltipProps, Typography } from "@rp/react/web/mui/core"

type Props = {
  children: TooltipProps["children"]
  ownerName: string
  lastUpdated?: string
  disabled?: boolean
}
const SearchInfo = ({ children, ownerName, lastUpdated, disabled = false }: Props) => {
  return (
    <RPTooltip
      placement="bottom"
      arrow={false}
      slotProps={{
        tooltip: {
          sx: {
            maxWidth: "unset",
          },
        },
      }}
      title={
        <Box sx={{ display: "flex", p: 1.5 }}>
          <Box
            sx={{
              borderRight: "1px solid #FFFFFF52",
              pr: 1,
              width: 200,
            }}
          >
            <Typography variant="body2" color="grey.300" letterSpacing={0.25}>
              Owner
            </Typography>
            <Typography sx={{ overflow: "hidden", whiteSpace: "nowrap", textOverflow: "ellipsis" }}>
              {ownerName}
            </Typography>
          </Box>
          <Box
            sx={{
              pl: 1,
              width: 200,
            }}
          >
            <Typography variant="body2" color="grey.300" letterSpacing={0.25}>
              Last Updated
            </Typography>
            <Typography>
              {lastUpdated ? format(new Date(lastUpdated), "MMM dd, yyyy") : "Unknown"}
            </Typography>
          </Box>
        </Box>
      }
      disableFocusListener={disabled}
    >
      {children}
    </RPTooltip>
  )
}

export { SearchInfo }
